import { SwitchProps } from "@mui/material";
import { FC } from "react";
import { Control, Controller, UseControllerProps } from "react-hook-form";
import { IOSSwitch } from "SharedComponents/Switch";

interface FormIOSSwitchProps extends UseControllerProps {
  control: Control<any, any>;
}

const FormIOSSwitch: FC<FormIOSSwitchProps & SwitchProps> = ({
  control,
  defaultValue,
  rules,
  name,
  ...props
}) => {
  return (
    <Controller
      rules={rules}
      control={control}
      name={name}
      render={({ field }) => {
        return (
          <IOSSwitch
            {...props}
            checked={field.value}
            onChange={field.onChange}
          />
        );
      }}
    />
  );
};

export default FormIOSSwitch;
