import { ChangeEvent, Dispatch, SetStateAction, useState } from "react";
import { IconButton } from "@mui/material";
import { Clear } from "@mui/icons-material";

import { ActionButton } from "SharedComponents/CommonComponents";
import {
  SearchBlock,
  SearchInput,
  SearchWrapper,
  TeamHead,
  TeamHeadBottom,
  TeamHeadBottomLeft,
  TeamHeadBottomRight,
  TeamHeadTitle,
  TeamHeadTop,
} from "Containers/BusinessSettings/LayoutComponents";
import { Search, Sync } from "Assets/Icons";
import WATemplatesApi from "Apis/WATemplatesApi";
import { useWaTemplateApi, useWaTemplateData } from "Contexts/WhatsApp";
import { useSnacks } from "Contexts/Snack";

const LeadDataFieldHeader = ({
  isSearchActive,
  setSearchActive,
}: {
  isSearchActive: boolean;
  setSearchActive: Dispatch<SetStateAction<boolean>>;
}) => {
  const { setErrorMessage, setSuccessMessage } = useSnacks();
  const { totalWaTemplates } = useWaTemplateData();
  const {
    setWaTemplateFilter,
    setShowWaTemplateForm,
    setTotalWaTemplates,
    setWaTemplatesData,
  } = useWaTemplateApi();
  const [isSyncing, setSyncing] = useState<boolean>(false);
  const syncMessages = async () => {
    try {
      setSyncing(true);
      const res = await WATemplatesApi.syncWaTemplateMessage();
      if (res.success) {
        const res1 = await WATemplatesApi.getWaTemplateMessages();
        if (!res1.success) {
          throw Error(res.message);
        }
        setTotalWaTemplates(res1.payload.length);
        setWaTemplatesData(res1.payload);
        setSuccessMessage(res.message);
      } else {
        setErrorMessage(res.message);
      }
    } catch (error: any) {
      setErrorMessage(error.message);
    } finally {
      setSyncing(false);
    }
  };

  return (
    <TeamHead>
      <TeamHeadTop>
        <ActionButton
          disableIcon
          type="button"
          onClick={() => setShowWaTemplateForm(true)}
          sx={{ py: 0, px: 2, height: 36, mt: 0 }}
        >
          {"New Template Message"}
        </ActionButton>
      </TeamHeadTop>
      <TeamHeadBottom>
        {!isSearchActive && (
          <TeamHeadBottomLeft>
            <TeamHeadTitle>{`${totalWaTemplates} Template Messages`}</TeamHeadTitle>
          </TeamHeadBottomLeft>
        )}
        <TeamHeadBottomRight>
          {isSearchActive && (
            <SearchWrapper>
              <SearchBlock>
                <IconButton
                  disableTouchRipple
                  disableFocusRipple
                  disableRipple
                  sx={{ position: "absolute" }}
                >
                  <Search />
                </IconButton>
                <SearchInput
                  onChange={({
                    target: { value },
                  }: ChangeEvent<HTMLInputElement>) =>
                    setWaTemplateFilter(value)
                  }
                  placeholder="Search Template"
                />
                <IconButton
                  onClick={() => {
                    setWaTemplateFilter("");
                    setSearchActive(!isSearchActive);
                  }}
                  sx={{ position: "absolute", right: 0, top: 0 }}
                >
                  <Clear />
                </IconButton>
              </SearchBlock>
            </SearchWrapper>
          )}
          {!isSearchActive && (
            <IconButton
              onClick={() => setSearchActive((prevState) => !prevState)}
            >
              <Search />
            </IconButton>
          )}
          {!isSearchActive && (
            <ActionButton
              icon={<Sync color={isSyncing ? "#00000042" : "#4D4D4D"} />}
              variant="outlined"
              type="button"
              disabled={isSyncing}
              onClick={() => syncMessages()}
              sx={{
                alignSelf: "center",
                borderRadius: 4,
                borderColor: "#0000003b",
                color: "#000000de",
                flexDirection: "row-reverse",
                fontSize: 14,
                fontWeight: 400,
                letterSpacing: "0.02857em",
                lineHeight: "16px",
                gap: 1,
                height: 30,
                py: 0.625,
                px: 1.875,
                textTransform: "capitalize",
              }}
            >
              {isSyncing ? "Syncing..." : "Sync Messages"}
            </ActionButton>
          )}
        </TeamHeadBottomRight>
      </TeamHeadBottom>
    </TeamHead>
  );
};

export default LeadDataFieldHeader;
