import { AuthenticatedReq } from "Utils/Axios";

export default class AssetsApi {
  public static fileUpload = async (directory: string, data: any) => {
    return await AuthenticatedReq(`/asset?directory=${directory}`, "POST", {
      data,
    });
  };

  public static fileRemove = async (file_id: string) => {
    return await AuthenticatedReq(`/asset`, "DELETE", {
      data: { id: file_id },
    });
  };

  public static fileRemoveByURL = async (url: string) => {
    return await AuthenticatedReq(`/asset`, "DELETE", {
      data: { url },
    });
  };
}
