// import ChatFlowApi from "Apis/ChatFlowApi";
import { FC, useState } from "react";
import { Box } from "@mui/material";
import { DropEvent, FileRejection } from "react-dropzone";
import { Control, UseFormSetValue } from "react-hook-form";

import { useSnacks } from "Contexts/Snack";
import { AssetDirectories } from "Shared/Enums/Asset";
import AssetsApi from "Apis/AssetsApi";

import { FileType, FileUploadType } from "Shared/Enums/FileType";

import StyledDropZone from "./StyledDropZone/StyledDropZone";

interface FileProps {
  control: Control<any, any>;
  fileTypes?: FileUploadType;
  setValue: UseFormSetValue<any>;
  label?: string;
  defaultFieldName: string;
  fileURL: string;
  fileNameField?: string;
  width?: string;
  height?: string;
}

const FileUpload: FC<FileProps> = ({
  control,
  fileTypes = FileUploadType.IMAGE,
  setValue,
  label = "Upload Image",
  defaultFieldName,
  fileURL,
  fileNameField,
  width,
  height,
}) => {
  const { setErrorMessage } = useSnacks();
  const [fileName, setFileName] = useState<string>("");
  const onDropHandler = async (
    acceptedFiles: File[],
    fileRejections: FileRejection[],
    event: DropEvent
  ) => {
    try {
      if (acceptedFiles.length > 0) {
        const data = new FormData();
        data.append("file", acceptedFiles[0]);
        data.append("max_size", "10000");
        data.append("allowed_ext", FileType[fileTypes]);
        const res = await AssetsApi.fileUpload(
          AssetDirectories.ENGAGE_ASSETS,
          data
        );
        if (!res.success) {
          throw Error(res.message);
        }
        setFileName(res.payload.file_name);
        setValue(defaultFieldName, res.payload.url);
        if (fileNameField && fileNameField !== "") {
          setValue(fileNameField, res.payload.file_name);
        }
      }
    } catch (error: any) {
      console.error(error);
      setErrorMessage(error.message);
    }
  };

  const onRemoveHandler = async () => {
    try {
      if (fileURL !== "") {
        const res = await AssetsApi.fileRemoveByURL(fileURL);
        if (!res.success) {
          throw Error(res.message);
        }
        setValue(defaultFieldName, "");
      }
    } catch (error: any) {
      setValue(defaultFieldName, "");
      console.error(error);
      setErrorMessage(error.message);
    }
  };

  return (
    <Box
      sx={{
        transition: "all 0.2s ease-in-out",
        width: { xs: width ?? 157, lg: 180, xl: 200 },
        height: { xs: height ?? 157, lg: 180, xl: 200 },
        margin: "auto",
      }}
    >
      <StyledDropZone
        label={label}
        fileType={fileTypes}
        removeFile={onRemoveHandler}
        fileUrl={fileURL}
        fileName={fileName}
        onChange={onDropHandler}
      />
    </Box>
  );
};

export default FileUpload;
