/* eslint-disable react-hooks/exhaustive-deps */
import { useCallback, useEffect, useState } from "react";
import { Drawer, IconButton, Link, Typography } from "@mui/material";

import WATemplatesApi from "Apis/WATemplatesApi";
import FormTextField from "SharedComponents/FormComponents/FormTextField";
import FileUpload from "SharedComponents/FileUpload";
import { ActionArea, ActionButton } from "SharedComponents/CommonComponents";
import {
  ContentHeader,
  DrawerForm,
  DrawerWrapper,
  Field,
  FormTextFieldSX,
  HeaderTitle,
} from "Containers/BusinessSettings/LayoutComponents";
import { LeftArrow } from "Assets/Icons";
import { isBackDropOrEscapeKey } from "Utils";
import { useGlobal } from "Contexts/App";
import { useSnacks } from "Contexts/Snack";
import { useWaTemplateApi, useWaTemplateData } from "Contexts/WhatsApp";
import { WATemplatesComponentType } from "Shared/Enums/WhatsApp";
import { useForm } from "react-hook-form";
import { FileUploadType } from "Shared/Enums/FileType";
import { WATemplateComponent } from "Shared/Interfaces/WhatsApp";

import TemplatePreviewCard from "./TemplatePreviewCard";

interface WaTemplatePreviewProps {
  open: boolean;
  closeDrawer: () => void;
}

const WaTemplatePreview = ({ open, closeDrawer }: WaTemplatePreviewProps) => {
  const { isMobile, isTablet } = useGlobal();
  const variableRegX = /[{][{]\d{1,3}[}][}]/gm;
  const { setErrorMessage, setSuccessMessage } = useSnacks();
  const { previewWaTemplateId, showWaTemplatePreview } = useWaTemplateData();
  const {
    setPreviewWaTemplateData,
    setEditWaTemplateId,
    setShowWaTemplateForm,
  } = useWaTemplateApi();
  const [bodyCounter, setBodyCounter] = useState<number[]>([]);
  const formDefault: any = {
    components: [
      {
        format: "",
        text: "",
        type: WATemplatesComponentType.HEADER,
      },
      {
        text: "",
        type: WATemplatesComponentType.BODY,
      },
      {
        text: "",
        type: WATemplatesComponentType.FOOTER,
      },
      {
        buttons: [],
        type: WATemplatesComponentType.BUTTONS,
      },
    ],
  };
  const { control, handleSubmit, watch, setValue } = useForm<any>({
    defaultValues: formDefault,
  });
  const { components } = watch();
  const HEADER = components.find(
    ({ type }: WATemplateComponent) => type === WATemplatesComponentType.HEADER
  );
  const BODY = components.find(
    ({ type }: WATemplateComponent) => type === WATemplatesComponentType.BODY
  );
  const BUTTONS = components.find(
    ({ type }: WATemplateComponent) => type === WATemplatesComponentType.BUTTONS
  );
  const headerMedia = HEADER?.example?.header_handle?.[0];
  const conditions: boolean[] = [
    HEADER && HEADER["format"] !== "TEXT",
    HEADER &&
    HEADER["format"] === "TEXT" &&
    HEADER["text"] &&
    HEADER["text"] !== ""
      ? HEADER["text"].includes("{{1}}")
      : false,
    BODY &&
      BODY["text"] &&
      BODY["text"] !== "" &&
      BODY["text"].includes("{{1}}"),
    BUTTONS &&
      BUTTONS.buttons.length > 0 &&
      BUTTONS.buttons[0]?.type === "URL" &&
      BUTTONS.buttons[0]?.url.includes("{{1}}"),
  ].reduce((a: boolean, c: boolean) => {
    return a || c;
  }, false);

  const onSubmit = async (data: any) => {
    try {
      const components = data.components.map(({ active, ...itemData }: any) => {
        if (itemData.type === "HEADER") {
          delete itemData.example.header_files;
          return itemData;
        } else {
          return itemData;
        }
      });
      if (previewWaTemplateId) {
        const res = await WATemplatesApi.updateWaTemplateComponents(
          previewWaTemplateId,
          { components }
        );
        if (!res.success) {
          throw Error(res.message);
        }
        setSuccessMessage(res.message);
        closeDrawer();
      }
    } catch (error: any) {
      setErrorMessage(error.message);
    }
  };
  const fieldIdentify = useCallback(() => {
    if (components.length > 0) {
      components.forEach(({ type, text, buttons }: any) => {
        if (type === "BODY") {
          const matches = text.match(variableRegX);
          if (matches) {
            setBodyCounter(
              matches.map((_: string, index: number) => index + 1)
            );
          }
        }
      });
    }
  }, [components]);
  const fetchPreviewTemplate = useCallback(async () => {
    try {
      if (previewWaTemplateId) {
        const res = await WATemplatesApi.getWaTemplateMessage(
          previewWaTemplateId
        );
        if (!res.success) {
          throw Error(res.message);
        }
        setPreviewWaTemplateData(res.payload);
        setValue("components", res.payload.components);
      }
    } catch (error: any) {
      setErrorMessage(error.message);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [previewWaTemplateId]);

  useEffect(() => {
    fetchPreviewTemplate();
  }, [fetchPreviewTemplate]);

  useEffect(() => {
    fieldIdentify();
  }, [fieldIdentify]);

  return (
    <Drawer
      anchor={"right"}
      open={open}
      onClose={(_, reason) => {
        if (!isBackDropOrEscapeKey(reason) || showWaTemplatePreview) {
          closeDrawer();
        }
      }}
      sx={{
        zIndex: "modal",
        "& .MuiPaper-root.MuiDrawer-paper": {
          flexDirection: "row-reverse",
        },
      }}
    >
      <DrawerWrapper sx={{ width: { xs: "100vw", md: 360 } }}>
        {open && (
          <ContentHeader>
            <IconButton
              onClick={() => {
                closeDrawer();
              }}
              sx={{ width: 32, height: 32 }}
            >
              <LeftArrow />
            </IconButton>
            <HeaderTitle>
              {conditions && !showWaTemplatePreview
                ? `Enter Sample Values`
                : `Preview Template`}
            </HeaderTitle>
          </ContentHeader>
        )}
        {open && (
          <DrawerForm onSubmit={handleSubmit(onSubmit)}>
            {(!conditions || showWaTemplatePreview || isMobile) && (
              <TemplatePreviewCard components={components} />
            )}
            {!showWaTemplatePreview &&
              HEADER &&
              (HEADER["format"] === "IMAGE" ||
                HEADER["format"] === "VIDEO" ||
                HEADER["format"] === "DOCUMENT") && (
                <Field>
                  <FileUpload
                    setValue={setValue}
                    control={control}
                    fileTypes={
                      HEADER["format"] === "IMAGE"
                        ? FileUploadType.IMAGE
                        : HEADER["format"] === "VIDEO"
                        ? FileUploadType.VIDEO
                        : FileUploadType.FILE
                    }
                    label={`Drag & Drop ${HEADER["format"]
                      .charAt(0)
                      .toUpperCase()}${HEADER["format"]
                      .slice(1)
                      .toLowerCase()}`}
                    defaultFieldName="components[0].example.header_handle[0]"
                    fileNameField="components[0].example.header_files[0]"
                    fileURL={headerMedia ?? ""}
                    width="100%"
                  />
                </Field>
              )}
            {!showWaTemplatePreview &&
              HEADER &&
              HEADER["format"] === "TEXT" &&
              HEADER["text"].includes("{{1}}") && (
                <Field>
                  <FormTextField
                    control={control}
                    name="components[0].example.header_text[0]"
                    label="Header Variable 1"
                    placeholder="Header {{1}}"
                    required={true}
                    sx={FormTextFieldSX}
                  />
                </Field>
              )}
            {!showWaTemplatePreview &&
              bodyCounter.length > 0 &&
              bodyCounter.map((i: number, index: number) => (
                <Field key={`bodyVar{{${i}}}`}>
                  <FormTextField
                    control={control}
                    name={`components[1].example.body_text[${index}]`}
                    label={`Body Variable ${i}`}
                    placeholder={`Body ${i}`}
                    required={true}
                    sx={FormTextFieldSX}
                  />
                </Field>
              ))}
            {!showWaTemplatePreview &&
              BUTTONS &&
              BUTTONS.buttons &&
              BUTTONS.buttons.length > 0 &&
              BUTTONS.buttons.map((button: any, index: number) => {
                return button.type === "URL" ? (
                  <Field key={index}>
                    {/* {button.example?.[0] ?? ""} */}
                    <FormTextField
                      control={control}
                      name={`components[3].buttons[${index}].example[0]`}
                      label={`URL Button ${index + 1}`}
                      placeholder={`Url button ${index + 1} variable 1`}
                      required={true}
                      sx={FormTextFieldSX}
                    />
                  </Field>
                ) : null;
              })}
            {!showWaTemplatePreview && (
              <ActionArea>
                <ActionButton type="submit">
                  {"Submit for Approval"}
                </ActionButton>
              </ActionArea>
            )}
            {!showWaTemplatePreview && (
              <ActionArea
                sx={{
                  alignItems: "center",
                  justifyContent: "center",
                  gap: 0.625,
                  my: 2,
                }}
              >
                <Typography
                  component="p"
                  sx={{
                    color: "#686C72",
                    fontSize: 14,
                    fontWeight: 400,
                    lineHeight: "16px",
                  }}
                >
                  {"Not happy?"}
                </Typography>
                <Link
                  type="button"
                  component="button"
                  variant="body2"
                  underline="none"
                  onClick={() => {
                    setEditWaTemplateId(previewWaTemplateId);
                    setShowWaTemplateForm(true);
                    closeDrawer();
                  }}
                >
                  {"Continue Editing"}
                </Link>
              </ActionArea>
            )}
          </DrawerForm>
        )}
      </DrawerWrapper>
      {!showWaTemplatePreview && open && conditions && isTablet && (
        <DrawerWrapper
          sx={{
            borderRight: "1px solid #24272c1a",
            py: 11.25,
            px: 6.25,
            width: 360,
          }}
        >
          <TemplatePreviewCard components={components} />
        </DrawerWrapper>
      )}
    </Drawer>
  );
};

export default WaTemplatePreview;
