/* eslint-disable react-hooks/exhaustive-deps */
import { useCallback, useEffect, useState } from "react";
import { Card, CardHeader, IconButton, MenuItem } from "@mui/material";
import { Close } from "@mui/icons-material";
import { Control, UseFormSetValue } from "react-hook-form";
import { MuiTelInput, MuiTelInputCountry } from "mui-tel-input";

import {
  Field,
  FormSelectFieldSX,
  FormTextFieldSX,
  FieldWrapper,
  VariableButton,
  FieldLimiter,
  FieldLimit,
  FormTelFieldSX,
} from "Containers/BusinessSettings/LayoutComponents";
import { useGlobal } from "Contexts/App";
import { WATemplateButtonCtaType } from "Shared/Interfaces/WhatsApp";
import FormTextField from "SharedComponents/FormComponents/FormTextField";

interface CtaButtonCardProps {
  index: number;
  type: string;
  url?: string;
  phone_number?: string;
  waTemplateButtonCtaTypes: WATemplateButtonCtaType[];
  control: Control<any, any>;
  setValue: UseFormSetValue<any>;
  removeCallAction: (index: number) => void;
}

const CtaButtonCard = ({
  index,
  type,
  url,
  phone_number,
  waTemplateButtonCtaTypes,
  control,
  setValue,
  removeCallAction,
}: CtaButtonCardProps) => {
  const { country } = useGlobal();
  const variableRegX = /[{][{]\d{1,3}[}][}]/gm;
  const [ctaVariableCount, setCtaVariableCount] = useState<number>(0);
  const addCtaVariable = () => {
    if (ctaVariableCount === 1) {
      return;
    }
    const newValue = `${url}{{${1}}}`;
    setValue(`components[3].buttons[${index}].url`, newValue);
    setCtaVariableCount(1);
  };
  const leftCtaVariable = useCallback(() => {
    if (url) {
      const matches = url.match(variableRegX);
      if (matches && matches.length > 0) {
        setCtaVariableCount(1);
      } else {
        setCtaVariableCount(0);
      }
    }
  }, [url]);

  useEffect(() => {
    leftCtaVariable();
  }, [leftCtaVariable]);

  return (
    <Card
      key={index}
      sx={{
        border: "1px solid rgba(0, 0, 0, 0.12)",
        borderRadius: 4,
        boxShadow: "0px 0px 20px 1px rgba(0, 0, 0, 0.1)",
        mb: 3,
        p: 2,
        pb: 3,
      }}
      elevation={0}
    >
      <CardHeader
        title={`Call to Action ${index + 1}`}
        action={
          <IconButton
            aria-label={`Remove Call to Action ${index + 1}`}
            onClick={() => removeCallAction(index)}
          >
            <Close />
          </IconButton>
        }
        sx={{
          p: 0,

          "& .MuiCardHeader-title": {
            fontSize: 16,
            fontWeight: 500,
            lineHeight: "22px",
          },
        }}
      />
      <Field sx={{ my: 2 }}>
        <FormTextField
          select
          control={control}
          label="Action Type"
          name={`components[3].buttons[${index}].type`}
          sx={FormSelectFieldSX}
          required={true}
          defaultValue={type}
        >
          <MenuItem value="" disabled>
            {"Select Action Type"}
          </MenuItem>
          {waTemplateButtonCtaTypes.map(({ value, name }, index) => (
            <MenuItem key={index} value={value}>
              {name}
            </MenuItem>
          ))}
        </FormTextField>
      </Field>
      {type !== "" && (
        <Field sx={{ my: 2 }}>
          <FormTextField
            control={control}
            name={`components[3].buttons[${index}].text`}
            label="Label"
            required={true}
            sx={FormTextFieldSX}
          />
        </Field>
      )}
      {type === "PHONE_NUMBER" && (
        <Field sx={{ my: 2 }}>
          <MuiTelInput
            label={"Phone"}
            placeholder={"Phone"}
            required={true}
            fullWidth={true}
            defaultCountry={country as MuiTelInputCountry}
            value={phone_number ?? ""}
            onChange={(newValue: string) => {
              setValue(
                `components[3].buttons[${index}].phone_number`,
                newValue.replaceAll(" ", "")
              );
            }}
            variant={"filled"}
            sx={FormTelFieldSX}
          />
        </Field>
      )}
      {type === "URL" && (
        <Field sx={{ my: 2 }}>
          <FieldWrapper>
            <FormTextField
              control={control}
              name={`components[3].buttons[${index}].url`}
              label="URL"
              required={true}
              sx={{ ...FormTextFieldSX, pb: 2, height: "auto" }}
            />
            <VariableButton
              size="small"
              focusRipple={false}
              className="add-variable"
              disabled={ctaVariableCount === 1}
              onClick={addCtaVariable}
            >
              {"ADD VARIABLE"}
            </VariableButton>
          </FieldWrapper>
          <FieldLimiter>
            <FieldLimit>
              {url ? url.length : 0}
              {"/60 Characters"}
            </FieldLimit>
            <FieldLimit>
              {ctaVariableCount}
              {"/1 Variable"}
            </FieldLimit>
          </FieldLimiter>
        </Field>
      )}
    </Card>
  );
};

export default CtaButtonCard;
