import { Card, CardHeader, IconButton } from "@mui/material";
import { Close } from "@mui/icons-material";
import { Control } from "react-hook-form";

import {
  Field,
  FormTextFieldSX,
} from "Containers/BusinessSettings/LayoutComponents";
import FormTextField from "SharedComponents/FormComponents/FormTextField";

interface QrButtonCardProps {
  index: number;
  control: Control<any, any>;
  removeQuickReply: (index: number) => void;
}

const QrButtonCard = ({
  index,
  control,
  removeQuickReply,
}: QrButtonCardProps) => {
  return (
    <Card
      key={index}
      sx={{
        border: "1px solid rgba(0, 0, 0, 0.12)",
        borderRadius: 4,
        boxShadow: "0px 0px 20px 1px rgba(0, 0, 0, 0.1)",
        mb: 3,
        p: 2,
        pb: 3,
      }}
      elevation={0}
    >
      <CardHeader
        title={`Quick Reply ${index + 1}`}
        action={
          <IconButton
            aria-label={`Remove Quick Reply ${index + 1}`}
            onClick={() => removeQuickReply(index)}
          >
            <Close />
          </IconButton>
        }
        sx={{
          p: 0,

          "& .MuiCardHeader-title": {
            fontSize: 16,
            fontWeight: 500,
            lineHeight: "22px",
          },
        }}
      />
      <Field sx={{ my: 2 }}>
        <FormTextField
          control={control}
          name={`components[3].buttons[${index}].text`}
          label="Label"
          required={true}
          sx={FormTextFieldSX}
        />
      </Field>
    </Card>
  );
};

export default QrButtonCard;
