import { Fragment, useEffect, useRef, useState } from "react";
import { Box, Button, CardMedia, Link } from "@mui/material";

import {
  BodyText,
  FooterText,
  HeaderMediaSx,
  HeaderImgWrap,
  HeaderText,
  MediaWrapperSx,
  PreviewAction,
  PreviewCard,
  PreviewButtonSx,
} from "Containers/BusinessSettings/LayoutComponents";
import { WATemplateComponent } from "Shared/Interfaces/WhatsApp";
import { WATemplatesComponentType } from "Shared/Enums/WhatsApp";
import { Play, Pause, Shortcut } from "Assets/Icons";
import { formatMessage } from "Utils";

const TemplatePreviewCard = ({ components }: any) => {
  const videoRef = useRef<HTMLVideoElement>(null);
  const [playVideo, setPlayVideo] = useState(false);
  const [videoHover, setVideoHover] = useState(false);
  const [videoState, setVideoState] = useState("");
  const HEADER = components.find(
    ({ type }: WATemplateComponent) => type === WATemplatesComponentType.HEADER
  );
  const BODY = components.find(
    ({ type }: WATemplateComponent) => type === WATemplatesComponentType.BODY
  );
  const FOOTER = components.find(
    ({ type }: WATemplateComponent) => type === WATemplatesComponentType.FOOTER
  );
  const BUTTONS = components.find(
    ({ type }: WATemplateComponent) => type === WATemplatesComponentType.BUTTONS
  );
  const headerVars: any[] = ((HEADER) => {
    if (HEADER) {
      const { example } = HEADER;
      if (example) {
        const { header_text } = example;
        if (header_text) {
          return header_text;
        } else {
          return [];
        }
      } else {
        return [];
      }
    } else {
      return [];
    }
  })(HEADER);
  const headerMedia = ((HEADER) => {
    if (HEADER) {
      const { example } = HEADER;
      if (example) {
        const { header_handle } = example;
        if (header_handle && header_handle.length > 0) {
          const [media] = header_handle;
          return media;
        } else {
          return "";
        }
      } else {
        return "";
      }
    } else {
      return "";
    }
  })(HEADER);
  const headerFileName = ((HEADER) => {
    if (HEADER) {
      const { example } = HEADER;
      if (example) {
        const { header_files } = example;
        if (header_files && header_files.length > 0) {
          const [fileName] = header_files;
          return fileName;
        } else {
          return "";
        }
      } else {
        return "";
      }
    } else {
      return "";
    }
  })(HEADER);
  const bodyVars: any[] = ((BODY) => {
    if (BODY) {
      const { example } = BODY;
      if (example) {
        const { body_text } = example;
        if (body_text) {
          return body_text;
        } else {
          return [];
        }
      } else {
        return [];
      }
    } else {
      return [];
    }
  })(BODY);
  const headerText = ((headerVars) =>
    HEADER
      ? headerVars.reduce(
          (accumulator: string, currentValue: string, index: number) =>
            currentValue && currentValue !== ""
              ? accumulator.replace(`{{${index + 1}}}`, currentValue)
              : accumulator,
          HEADER.text
        )
      : "")(headerVars);
  const bodyMsg = ((bodyVars) =>
    BODY
      ? bodyVars.reduce(
          (accumulator: string, currentValue: string, index: number) =>
            currentValue && currentValue !== ""
              ? accumulator.replace(`{{${index + 1}}}`, currentValue)
              : accumulator,
          BODY.text
        )
      : "")(bodyVars);

  useEffect(() => {
    if (videoRef.current) {
      videoRef.current.addEventListener("playing", () => {
        setVideoState("playing");
      });
      videoRef.current.addEventListener("pause", () => {
        setVideoState("");
      });
      videoRef.current.addEventListener("ended", () => {
        setVideoState("");
      });
      playVideo ? videoRef.current.play() : videoRef.current.pause();
    }
  }, [playVideo]);

  return (
    <PreviewCard>
      {HEADER && HEADER["format"] === "IMAGE" && (
        <HeaderImgWrap>
          {headerMedia && headerMedia !== "" && (
            <CardMedia
              component="img"
              alt="header-img"
              image={headerMedia}
              sx={HeaderMediaSx}
            />
          )}
        </HeaderImgWrap>
      )}
      {HEADER && HEADER["format"] === "VIDEO" && (
        <Link
          underline="none"
          component="button"
          type="button"
          onClick={() => setPlayVideo(!playVideo)}
          onMouseEnter={() => setVideoHover(true)}
          onMouseLeave={() => setVideoHover(false)}
          sx={MediaWrapperSx}
        >
          <CardMedia
            component="video"
            ref={videoRef}
            className="video"
            disablePictureInPicture={true}
            sx={{
              ...HeaderMediaSx,
              height: 130,
            }}
          >
            {headerMedia && headerMedia !== "" && (
              <source src={headerMedia} type="video/mp4" />
            )}
          </CardMedia>
          <Box position="absolute" display="flex">
            {videoState === "" && <Play />}
            {videoHover && videoState === "playing" && <Pause />}
          </Box>
        </Link>
      )}
      {HEADER && HEADER["format"] === "DOCUMENT" && (
        <Link
          underline="none"
          className={`media-wrapper doc`}
          href={headerMedia}
          target="_blank"
          sx={{
            ...MediaWrapperSx,
            backgroundColor: "#F5F5F5",
            color: "#A1AEB4",
            fontSize: 90,
            fontWeight: 900,
            lineHeight: "96px",
          }}
        >
          {headerMedia && headerMedia !== ""
            ? headerFileName.split(".").pop().slice(0, 4).toUpperCase()
            : ""}
        </Link>
      )}
      {HEADER && HEADER["format"] === "TEXT" && (
        <HeaderText>{headerText}</HeaderText>
      )}
      <BodyText>{formatMessage(bodyMsg)}</BodyText>
      {FOOTER && FOOTER["text"] !== "" && (
        <FooterText>{FOOTER["text"]}</FooterText>
      )}
      {BUTTONS && BUTTONS.buttons.length > 0 && (
        <PreviewAction>
          {BUTTONS.buttons.map((button: any, index: number) => {
            return (
              <Fragment key={index}>
                {button.type === "QUICK_REPLY" ? (
                  <Button
                    variant="outlined"
                    type="button"
                    sx={{
                      ...PreviewButtonSx,

                      color: "#767676",
                      minWidth: "calc(50% - 8px)",
                    }}
                  >
                    {button.text}
                  </Button>
                ) : button.type === "PHONE_NUMBER" ? (
                  <Button
                    href={`tel:${button.phone_number}`}
                    variant="outlined"
                    sx={{
                      ...PreviewButtonSx,

                      color: "#32C5FF",
                      minWidth: "100%",
                    }}
                  >
                    {button.text}
                  </Button>
                ) : (
                  <Button
                    href={
                      button.example
                        ? button.example.reduce(
                            (
                              accumulator: string,
                              currentValue: string,
                              index: number
                            ) =>
                              currentValue && currentValue !== ""
                                ? accumulator.replace(
                                    `{{${index + 1}}}`,
                                    currentValue
                                  )
                                : accumulator,
                            button.url
                          )
                        : ""
                    }
                    variant="outlined"
                    target="_blank"
                    rel="noreferrer"
                    sx={{
                      ...PreviewButtonSx,

                      color: "#32C5FF",
                      minWidth: "100%",
                    }}
                  >
                    <Shortcut />
                    {button.text}
                  </Button>
                )}
              </Fragment>
            );
          })}
        </PreviewAction>
      )}
    </PreviewCard>
  );
};

export default TemplatePreviewCard;
