import { useState } from "react";
import { CardMedia } from "@mui/material";

const Img = ({ src, alt, ...rest }: any) => {
  const fallBackImg = "/static/images/svg/fallback-img.svg";
  const [useSrc, setUseSrc] = useState<boolean>(true);

  return (
    <CardMedia
      component="img"
      image={useSrc ? src : fallBackImg}
      alt={alt}
      onError={() => setUseSrc(false)}
      {...rest}
    />
  );
};

export default Img;
