import { InsertDriveFileOutlined } from "@mui/icons-material";
import { Button } from "@mui/material";
import clsx from "clsx";
import { FC } from "react";
import { ZoneProps } from ".";
import classes from "./Styles.module.css";

const DocumentZone: FC<ZoneProps> = ({
  fileUrl,
  removeFile,
  fileName,
}: any) => {
  return (
    <div className="d-flex justify-content-center" style={{ height: "100%" }}>
      <div className={classes.File_Preview}>
        <div className="d-flex align-items-center">
          <InsertDriveFileOutlined fontSize="large" />{" "}
          <p>{fileName !== "" ? fileName : fileUrl?.split("/").pop()}</p>
        </div>
        <Button
          className={clsx("mt-1", classes.RemoveButton)}
          onClick={removeFile}
        >
          Remove
        </Button>
      </div>
    </div>
  );
};

export default DocumentZone;
