import { AuthenticatedReq } from "Utils/Axios";

export default class WATemplatesApi {
  public static getWaTemplateMessages = async () => {
    return await AuthenticatedReq(`/wa-templates`, "GET");
  };

  public static getWaTemplateMessage = async (editWaTemplateId: number) => {
    return await AuthenticatedReq(`/wa-templates/${editWaTemplateId}`, "GET");
  };

  public static syncWaTemplateMessage = async () => {
    return await AuthenticatedReq(`/wa-templates/sync-templates`, "GET");
  };

  public static createWaTemplateMessage = async (data: any) => {
    return await AuthenticatedReq(`/wa-templates/create-template`, "POST", {
      data,
    });
  };

  public static updateWaTemplateMessage = async (
    editWaTemplateId: number,
    data: any
  ) => {
    return await AuthenticatedReq(
      `/wa-templates/update-template-form/${editWaTemplateId}`,
      "PUT",
      { data }
    );
  };

  public static updateWaTemplateComponents = async (
    editWaTemplateId: number,
    data: any
  ) => {
    return await AuthenticatedReq(
      `/wa-templates/update/${editWaTemplateId}`,
      "PUT",
      { data }
    );
  };

  public static deleteWaTemplateMessage = async (
    deleteWaTemplateId: number
  ) => {
    return await AuthenticatedReq(
      `/wa-templates/delete/${deleteWaTemplateId}`,
      "DELETE"
    );
  };
}
