import { Fragment, MouseEvent, useState } from "react";
import {
  Menu,
  MenuItem,
  DialogTitle,
  DialogContent,
  DialogActions,
  Dialog,
  Button,
  ListItem,
  ListItemText,
  ListItemIcon,
  IconButton,
  Chip,
} from "@mui/material";
import { MoreVert } from "@mui/icons-material";

import WATemplatesApi from "Apis/WATemplatesApi";
import { useSnacks } from "Contexts/Snack";
import { useWaTemplateApi } from "Contexts/WhatsApp";
import { WATemplateStatusColor } from "Shared/Enums/WhatsApp";
import { WATemplateDataInterface } from "Shared/Interfaces/WhatsApp";

const LeadDataFieldItem = ({
  template_status,
  name,
  id,
  description,
}: WATemplateDataInterface) => {
  const { setErrorMessage, setSuccessMessage } = useSnacks();
  const {
    setEditWaTemplateId,
    setShowWaTemplateForm,
    setPreviewWaTemplateId,
    setShowWaTemplatePreview,
  } = useWaTemplateApi();
  // 3dot menu state
  const [anchorEl, setAnchorEl] = useState<HTMLButtonElement | null>(null);
  //dialog state for delete
  const [openConfirmDialog, setOpenConfirmDialog] = useState(false);
  //function for opening confirm box for delete
  const handleClickOpen = () => {
    setOpenConfirmDialog(true);
  };
  //function for closing confirm box for delete
  const handleClickClose = () => {
    setOpenConfirmDialog(false);
  };
  //function for opening 3 dot menu
  const handleClick = (event: MouseEvent<HTMLButtonElement>) => {
    setAnchorEl(event.currentTarget);
  };
  //function for closing 3 dot menu
  const handleClose = () => {
    setAnchorEl(null);
  };
  const deleteWaTemplateMessage = async () => {
    try {
      const res = await WATemplatesApi.deleteWaTemplateMessage(id);
      if (!res.success) {
        throw Error(res.message);
      }
      setSuccessMessage(res.message);
      setTimeout(() => window.location.reload(), 1000);
    } catch (error: any) {
      setErrorMessage(error.message);
    } finally {
      handleClickClose();
    }
  };

  return (
    <Fragment>
      <ListItem component="div" sx={{ px: { xs: 0, md: 2 } }}>
        <ListItemText
          primary={
            <div className="primary-block">
              <h2 className={"list-title"}>{name}</h2>
              <Chip
                label={template_status}
                size="small"
                sx={{
                  bgcolor: WATemplateStatusColor[template_status],
                  alignItems: "center",
                  color: "#FFFFFF",
                  borderRadius: 1,
                  height: 20,

                  "& .MuiChip-label": {
                    px: 0.5,
                    fontSize: 10,
                    fontWeight: 900,
                    lineHeight: "12px",
                  },
                }}
              />
            </div>
          }
          secondary={description}
          sx={{
            "& .primary-block": {
              alignItems: "center",
              display: "flex",
              flexWrap: "wrap",
              gap: 1.25,
              marginBottom: 0.75,

              "& .list-title": {
                color: "text.secondary",
                fontSize: 15,
                fontWeight: 500,
                lineHeight: "18px",
              },
            },

            "& .MuiListItemText-secondary": {
              color: "text.secondary",
              fontSize: 12,
              fontWeight: 400,
              lineHeight: "15px",
            },
          }}
        />
        <ListItemIcon>
          <IconButton
            // disabled
            onClick={handleClick}
          >
            <MoreVert />
          </IconButton>
        </ListItemIcon>
      </ListItem>
      {Boolean(anchorEl) && (
        <Menu
          anchorEl={anchorEl}
          open={Boolean(anchorEl)}
          onClose={handleClose}
          keepMounted
          MenuListProps={{
            "aria-labelledby": "Options",
          }}
          anchorOrigin={{
            vertical: "top",
            horizontal: "right",
          }}
          transformOrigin={{
            vertical: "top",
            horizontal: "right",
          }}
          sx={{
            "& .MuiPaper-root": { minWidth: 120 },
          }}
        >
          {template_status === "DRAFT" && (
            <MenuItem
              onClick={() => {
                setEditWaTemplateId(id);
                setShowWaTemplateForm(true);
                handleClose();
              }}
            >
              {"Edit"}
            </MenuItem>
          )}
          {(template_status === "SUBMITTED" ||
            template_status === "APPROVED") && (
            <MenuItem
              onClick={() => {
                setPreviewWaTemplateId(id);
                setShowWaTemplatePreview(true);
                handleClose();
              }}
            >
              {"Preview"}
            </MenuItem>
          )}
          <MenuItem
            onClick={() => {
              handleClickOpen();
              handleClose();
            }}
          >
            {"Delete"}
          </MenuItem>
        </Menu>
      )}
      <Dialog
        open={openConfirmDialog}
        aria-labelledby="responsive-dialog-title"
      >
        <DialogTitle id="responsive-dialog-title">{"Delete"}</DialogTitle>
        <DialogContent>{"Are you sure you want to delete?"}</DialogContent>
        <DialogActions>
          <Button
            color="secondary"
            onClick={() => {
              handleClickClose();
            }}
          >
            {"Cancel"}
          </Button>
          <Button
            onClick={async () => {
              await deleteWaTemplateMessage();
            }}
            autoFocus
          >
            {"Delete"}
          </Button>
        </DialogActions>
      </Dialog>
    </Fragment>
  );
};

export default LeadDataFieldItem;
